import { Record, Union } from "./.fable/fable-library.3.1.12/Types.js";
import { int32_type, bool_type, record_type, list_type, union_type, string_type, array_type, class_type } from "./.fable/fable-library.3.1.12/Reflection.js";
import { printf, toText } from "./.fable/fable-library.3.1.12/String.js";
import { ofList, empty } from "./.fable/fable-library.3.1.12/Set.js";
import { comparePrimitives } from "./.fable/fable-library.3.1.12/Util.js";
import { singleton } from "./.fable/fable-library.3.1.12/List.js";

export class AlbumState extends Union {
    constructor(tag, ...fields) {
        super();
        this.tag = (tag | 0);
        this.fields = fields;
    }
    cases() {
        return ["Loading", "Loaded", "LoadFailed"];
    }
}

export function AlbumState$reflection() {
    return union_type("Model.AlbumState", [], AlbumState, () => [[], [["Item", array_type(class_type("Album.IMediaInfo"))]], [["Item", string_type]]]);
}

export function AlbumState__get_AsString(this$) {
    switch (this$.tag) {
        case 1: {
            return "Album Loaded";
        }
        case 2: {
            return toText(printf("Album Load Failed (%s)"))(this$.fields[0]);
        }
        default: {
            return "Album Loading";
        }
    }
}

export class ModelState extends Union {
    constructor(tag, ...fields) {
        super();
        this.tag = (tag | 0);
        this.fields = fields;
    }
    cases() {
        return ["Loading", "Loaded", "Error"];
    }
}

export function ModelState$reflection() {
    return union_type("Model.ModelState", [], ModelState, () => [[], [], [["Item", list_type(class_type("System.Exception"))]]]);
}

export class Model extends Record {
    constructor(state, albumState, lang, completed, flags) {
        super();
        this.state = state;
        this.albumState = albumState;
        this.lang = lang;
        this.completed = completed;
        this.flags = flags;
    }
}

export function Model$reflection() {
    return record_type("Model.Model", [], Model, () => [["state", ModelState$reflection()], ["albumState", AlbumState$reflection()], ["lang", string_type], ["completed", class_type("Microsoft.FSharp.Collections.FSharpSet`1", [string_type])], ["flags", class_type("Microsoft.FSharp.Collections.FSharpSet`1", [string_type])]]);
}

export class Msg extends Union {
    constructor(tag, ...fields) {
        super();
        this.tag = (tag | 0);
        this.fields = fields;
    }
    cases() {
        return ["Ignore", "CheckInitTaskDone", "InitError", "LoadAlbumResponse", "SwitchLanguage", "Completed", "SetFlag", "TriggerAfter"];
    }
}

export function Msg$reflection() {
    return union_type("Model.Msg", [], Msg, () => [[], [], [["Item", class_type("System.Exception")]], [["Item", class_type("Album.IResult")]], [["Item", string_type]], [["Item", string_type]], [["Item1", string_type], ["Item2", bool_type]], [["ms", int32_type], ["Item2", Msg$reflection()]]]);
}

export function initModel(arg) {
    return new Model(new ModelState(0), new AlbumState(0), "unspecified", empty({
        Compare: (x, y) => comparePrimitives(x, y),
    }), ofList(singleton("playButtonIsShown"), {
        Compare: (x_1, y_1) => comparePrimitives(x_1, y_1),
    }));
}

